import Component from "./Component";

export default class TextComponent extends Component {

    // all HTML elements that can contain text except
    // '<script>' and '<style>'
    static _ALLOWED_TYPES = [
        'tt', 'i', 'b', 'big', 'small', 'em', 'strong',
        'dfn', 'code', 'samp', 'kdb', 'var', 'cite', 'abbr',
        'acronym', 'sub', 'sup', 'span', 'bdo', 'address',
        'div', 'a', 'object', 'p', 'h1', 'h2', 'h3', 'h4',
        'h5', 'h6', 'pre', 'q', 'ins', 'del', 'dt', 'dd', 'li',
        'label', 'option', 'textarea', 'fieldset', 'legend', 'button',
        'caption', 'td', 'th', 'title', 'blockquate'
    ];

    constructor(textType, properties) {
        if (!TextComponent._ALLOWED_TYPES.includes(textType)) {
            throw new Error('String value cannot be assigned to type "' + textType + '"');
        }
        super(textType, properties);
        if (properties.hasOwnProperty('text')) {
            this._domElement.textContent = properties.text;
        }
    }

    set text(value) {
        this._domElement.textContent = value;
    }

    get text() {
        return this._domElement.textContent;
    }

}