import Component from "./Component";

export default class EventComponent extends Component {

    #eventHandlers;

    constructor(type, properties) {
        if (!properties.hasOwnProperty('eventHandlers')) {
            throw new Error('Component must receive an object with valid event handlers');
        }

        super(type, properties);

        this.#eventHandlers = [];

        if (Array.isArray(properties.eventHandlers)) {
            for (let eventHandler in properties.eventHandlers) {
                this.#process_handler(properties.eventHandlers[eventHandler]);
            }
        } else if (typeof properties.eventHandlers === 'object') {
            this.#process_handler(properties.eventHandlers);
        } else {
            throw new Error('Unknown type of  the "eventHandlers" object');
        }
    }

    addEventHandler(eventHandler) {
        this.#process_handler(eventHandler);
    }

    removeEventHandler(eventHandlerName) {
        const element = this.#eventHandlers.find(item => item['name'] === eventHandlerName);
        if (element == null) {
            throw new Error('Component does not have handler "' + eventHandlerName + '" attached');
        }

        this._domElement.removeEventListener(element['name'], element['function'], element['options']);
    }

    #process_handler(handler) {
        const valid = EventComponent.#validHandler(handler);
        const element = this.#eventHandlers.find(item => item['name'] === valid['name']);
        if (element != null) {
            throw new Error('Component already has this event handler "' + valid['name'] + '"');
        }

        this.#eventHandlers.push(valid);
        this._domElement.addEventListener(valid['name'], valid['function'], valid['options']);
    }

    // handler must be an object with 'name', 'function' and 'options' properties
    // 'options' can be null
    static #validHandler(handler) {
        if (handler == null || !handler.hasOwnProperty('name') || !handler.hasOwnProperty('function')) {
            throw new Error('Handler must be an object with "name", "function" and ' +
                '"options" properties ("options" can be null');
        }

        if (!handler.hasOwnProperty('options')) {
            handler.options = null;
        }

        return handler;
    }

}